@import "@/scss/settings.scss";







































.nav{
    z-index: 99999999;
    &__item{
        z-index: 99999999;

        height: 50px;
        width: 50px;
        display: inline-flex;
        justify-content: center;
        align-items: center;

        color: white;
        font-size: 1rem;
        stroke: white;
        stroke-width: 3px;
        
        & > svg{
            stroke: white;
            transform: rotate(-90deg);
            transition: all .3s ease;
        }
        &:hover > svg{
            transform: rotate(-90deg) translate(-5px);
        }
    }
    .top{
        top:0;
        left: 50vw;
        position: fixed;
        transform: translate(-50%,0) rotate(180deg) ;
        &:before{
            content : ' ';
            display: block;
        }
        &:hover{

        }
    }
    .bottom{
        bottom:0;
        position: fixed;
        left: 50vw;
        transform: translate(-50%,0) rotate(0deg) ;
        &:before{
            content : ' ';
            display: block;
        }
        &:hover{
            
        }
    }
    .left{
        left:0;
        position: fixed;
        top:50vh;
        transform: translate(0,-50%) rotate(90deg) ;
        &:before{
            content : ' ';
            display: block;
        }
        &:hover{
            
        }
    }
    .right{
        right:0;
        position: fixed;
        top:50vh;
        transform: translate(0,-50%) rotate(270deg);
        &:before{
            content : ' ';
            display: block;
        }
        &:hover{
            
        }
    }
}
